.{
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
}

.App{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center ;
  align-items: center;
  flex-direction:row;
}

.App::-webkit-scrollbar {
    display: none; /* for Chrome, Safari, and Opera */
}

.{
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
}

.menu{
  width: 15vw;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: black;
   font-size: 0.8em;
   position: fixed;
padding-top: 10vh;

}

.content{
  width: 85vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
overflow: hidden;
right: 0;
top: 0;

}

.b1{
  width: 100%;
  height: 150vh;
  background-color: #4b596d ;
   display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.colorTest{

  width: 80px;
  height: 80px;
  margin-right: 5vw;
  font-size: 0.5em;
   display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.colorCont{
  width: 90vw;
  height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin-top: 10vh;
}


.home{

  width: 85vw;
  min-height: 100vh;
  overflow-y: scroll;
  color: darkgray;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 0.5em;
   font-family: 'Poppins', sans-serif;
   padding-left: 0;
}

.home::-webkit-scrollbar {
    display: none; /* for Chrome, Safari, and Opera */
}

.home {
    -ms-overflow-style: none; /* for Internet Explorer, Edge */
    scrollbar-width: none; /* for Firefox */
    overflow-y: scroll;
}

.home img{
  width: 25vw;
}

.text{
  max-width: 40vw;
  height: auto;
  padding-right: 5vw;

}


.photo{
  margin-right: 10vh;
  background-color: black;
  width: 50vw;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0;

}

.research{
  padding-top: 20vh;
width: 60vw;
  min-height: 100vh;
  overflow-y: scroll;
  color: darkgray;
  font-size: 0.6em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
   font-family: 'Poppins', sans-serif;
}

.research::-webkit-scrollbar {
    display: none; /* for Chrome, Safari, and Opera */
}

.research {
    -ms-overflow-style: none; /* for Internet Explorer, Edge */
    scrollbar-width: none; /* for Firefox */
    overflow-y: scroll;
}

.menu ul{
  list-style-type: none;
}

a {
   text-decoration: none;
}

